/**
 * 
 * @request
 *     "username": "zhangming"  string 
 *     "email": "zhangming@sensetime.com"  string 
 *     "password": "qwer1234"  string 
 *     "provider": ""  string 
 *     "provider_user_id":   string id
 *     "provider_user_login":   string 
 *  @return
 *     "login": True, # bool 
 *     "provider": "", # string 
 *     "provider_user_id":  # string id
 *     "provider_user_login":  # string 
 */
export const signup = (payload) => {
  const data = {};
  commonSignup.forEach((k) => {
    data[k] = payload[k];
  });
  console.warn('signup----', 'signup  ', '----', data);
  return axios({
    method: 'post',
    url: `/v1/signup`,
    data
  });
};

/**
 * 
 * @request
 *     "token": "",  string token
 *     "provider": "",  string 
 *     "provider_user_id":   string id
 *     "provider_user_login":   string 
 *  @return
 *      {}
 */
export const signup_verify = (payload = {}) => {
  const { token, invite_code, provider, provider_user_id, provider_user_login } = payload;
  return axios({
    method: 'post',
    url: `/v1/signup_verified`,
    data: {
      token,
      invite_code,
      provider,
      provider_user_id,
      provider_user_login
    }
  });
};


